import { mapMutations } from 'vuex';
export default {
	data() {
		return {
			mobile: '',
			isChecked: '1',
			btn: '获取验证码',
			time: 60,
			timer: null
		};
	},
	onUnload() {
		clearInterval(this.timer);
		this.timer = null;
	},
	methods: {
		...mapMutations(['setLogin']),
		onInput(e) {
			this.mobile = e.detail;
		},
		getCode() {
			console.log('获取验证码');
			if (this.time === 60) {
				this.$Fetch({
					url: 'login/code',
					data: { mobile: this.mobile },
					debug: true,
					success: res => {
						console.log(res);
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						});
						this.timer = setInterval(() => {
							this.time--;
							this.btn = this.time + 's后重新获取';
							if (this.time < 0) {
								clearInterval(this.timer);
								this.timer = null;
								this.time = 60;
								this.btn = '获取验证码';
							}
						}, 1000);
					}
				});
			}
		},
		onChange() {
			this.isChecked = this.isChecked == 1 ? '0' : '1';
		},
		onSubmit(e) {
			let that = this;
			console.log('提交数据：', e.detail.value);
			let data = e.detail.value;
			if (!data.server_people_id) {
				data.server_people_id = 0;
			}
			if (this.isChecked === '1') {
				this.$Fetch({
					url: 'login/register',
					data: e.detail.value,
					success: res => {
						console.log(res);
						uni.setStorage({
							key: 'TOKEN',
							data: res,
							success: function() {
								that.setLogin();
								uni.showToast({
									title: '注册成功',
									icon: 'success'
								});
								setTimeout(() => {
									uni.switchTab({
										url: '/pages/index/index'
									});
								}, 1500);
							}
						});
					}
				});
			} else {
				uni.showToast({
					title: '未同意用于协议，不可注册！',
					icon: 'none'
				});
			}
		}
	}
};
